import React, { Component, ReactNode } from 'react';
import Point from './models/Point';
import HexUtils from './HexUtils';
import { LayoutProvider } from './LayoutProvider';
import Orientation from './models/Orientation';

export type PointsMap = {
  [k in string] : string;
}

export type LayoutData = {
  origin?: Point | undefined;
  size: Point;
  spacing?: number | undefined;
  orientation: Orientation;
}

type Props = {
  children: ReactNode | ReactNode[];
  className?: string,
  flat: boolean,
  origin?: Point,
  size: Point,
  spacing?: number,
  customLayouts?: PointsMap;
};

class Layout extends Component<Props> {
  constructor(props: Props) {
    super(props);
  }
  static defaultProps = {
    size: new Point(10, 10),
    flat: true,
    spacing: 1.0,
    origin: new Point(0, 0)
  }

  layoutContext() {
    const { children, flat, className, customLayouts, ...rest } = this.props;
    const orientation = HexUtils.getOrientation(flat);
    const cornerCoords = HexUtils.calculateCoordinates(flat, this.props.size);
    const normalPoints: PointsMap = {};
    normalPoints["1x1Hex"] = cornerCoords.map(point => `${point.x},${point.y}`).join(' ');
    const points: PointsMap = {...normalPoints, ...customLayouts};
    const childLayout: LayoutData = Object.assign({}, rest, { orientation });

    return {
      layout: childLayout,
      points
    };
  }
  render() {
    const { children, className } = this.props;
    return (
      <LayoutProvider value={this.layoutContext()}>
        <g className={className}>
          {children}
        </g>
      </LayoutProvider>
    );
  }
}

export default Layout;
